.header {
  height: 80px;
  background-color: #fff;
}
.header .header-top {
  height: 40px;
  line-height: 40px;
  padding-right: 30px;
  text-align: right;
  border-bottom: 1px solid #1da57a;
}
.header .header-bottom {
  height: 40px;
  display: flex;
  align-items: center;
}
.header .header-bottom .header-bottom-left {
  position: relative;
  width: 25%;
  text-align: center;
  font-size: 20px;
}
.header .header-bottom .header-bottom-left::after {
  content: '';
  position: absolute;
  right: 50%;
  top: 100%;
  -webkit-transform: translateX(50%);
          transform: translateX(50%);
  border-top: 20px solid white;
  border-right: 20px solid transparent;
  border-bottom: 20px solid transparent;
  border-left: 20px solid transparent;
}
.header .header-bottom .header-bottom-right {
  width: 75%;
  text-align: right;
  margin-right: 30px;
}
.header .header-bottom .header-bottom-right img {
  margin: 0 15px;
  width: 30px;
  height: 20px;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab {
  height: 30px;
  line-height: 30px;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-tab a {
  color: #aaa;
}
.ant-tabs-tab-active {
  background-color: #fff !important;
}
.ant-tabs-tab-active a {
  color: #000 !important;
}
.ant-tabs.ant-tabs-card .ant-tabs-card-bar .ant-tabs-nav-container {
  height: 30px;
}
.ant-tabs-bar {
  margin: 0 !important;
}
